.state{
    font-size: 25px;
    padding: 1rem;
    background-color: rgb(117, 76, 76);
    color: white;
}

.video{
    height: 25rem;
    display: inline;
}

.container{
    height: calc(100vh - 5rem);
    padding: 2rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
}

.videoContainer{
    width: 25rem;
    height: 15rem;
    border-radius: 0.5rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.781);
    background-blend-mode: multiply;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.videoContainer video{
    display: none;
    height: calc(100% - 2rem);
}

.videoContainer p{
    text-align: center;
    color: rgb(175, 175, 175);
    font-size: 18px;
    height: 2rem;
    width: 100%;
    background-color: white;
    padding: 0;
    margin: 0;
}


.hidding-block{
    height: 100%;
    width: 100%;
    display: none;
    background-color: rgba(0, 0, 0, 0.24);
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}

.hidding-block-content{
    height: 50rem;
    width: 50rem;
    background-color: white;
    border-radius: 0.5rem;
    padding: 1rem;
}


.control{
    height: 5rem;
    background-color: rgb(41, 41, 41);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

i{
    color: white;
    font-size: 25px;
    border-radius: 50%;
}

.bi-x{
    color: black!important;
    margin: 0;
    padding: 0;
    font-size: 30px!important;
}
